fish.initData = function (){
    // roomRule 
    //         BaseConsume: 10,             // 
    //         EntranceRestrictions: 1000,  // 
    //         Rank: 1,                     // 
    //         MaxShell: 20,                // 
    //         LightningMaxFish: 5,         // 
    //         LightningRadius: 250,        // 
    //         ExplodeRadius: 125,          // 
    //         FirepillarWidth: 100,        // 
    //         ExcaliburMaxFish: 20,        // 
    //         ExcaliburRadius: 125,        // 
    //         CornucopiaStopTime: 10,      // 
    //         RuyiStopTime: 10,            // 
    //         StandbyTime:
    //         0                         // 
    //         1                         // 
    this.roomRule               = null;     //
    this.isSetAngle             = true;     //180
    this.isEnterRoom            = false;    //
    this.dialogPanelIsShow      = false;    //
    this.fishPoolData           = null;     //
    this.playerInfo             = {};       //
    this.gunType                = CONST.CannonType.Normal;// 0  1-4
    this.isInFreeze             = false;    //
    this.lockFishID             = null;     //
    this.lastLockFishID         = null;     //
    this.serverTime             = 0;        //
    this.gold                   = 0;        //
    this.baseConsume            = 0;        //    
    this.isLock                 = false;    //
    this.isAuto                 = false;    //
    this.isLockAutoChange       = false;    // 
    this.isRestoreIng           = {};       //
    this.cannonLevel            = 1;        //
    this.seatNum                = -1;       //
    this.roomId                 = 0;        //id  / 
    this.bullteNum              = 0;        //
    this.currentBullteType      = CONST.CannonType.Not;   //
    this.isYuRuyiRuning         = false;    //
    this.MaxBullte              = 0;        //
    this.showTimeMsg            = 0;        //
    this.exitTimeMsg            = 0;        //
    this.lastShootTime          = 0;        //
    this.quickMove              = false;    //
    this.isFishTideRuning       = false;    //
    this.startFire              = true;     // 
    this.json_fishResEdit       = null;     //
    this.json_fishTable         = null;     //
    this.json_fishLineGroup     = null;     //
    this.currPlayBgMusic        = -1;//
    this.bossIDList             = [];       //boss id 
    this.specialFishListBorn    = {};       //
    this.specialFishListBornLen = 0;        //
    this.isGunMoving0           = -1;       //0 ,  
    this.isGunMoving1           = -1;       //1 ,  
    this.isGunMoving2           = -1;       //2 ,  
    this.isGunMoving3           = -1;       //3 ,  
    this.playSoundTime          = 0;//  25
    this.curWaitTime            = 0;//
    this.tideRun                = 0;//
    this.specialBulletPool      = {};  //
    this.tidePlayCorrect        = {};  //
    this.isFireLaser            = false;  //
    this.lastBullteType0        = undefined;//0
    this.lastBullteType1        = undefined;//1
    this.lastBullteType2        = undefined;//2
    this.lastBullteType3        = undefined;//3
    this.isUpdateMoney0         = true;     //0    
    this.isUpdateMoney1         = true;     //1    
    this.isUpdateMoney2         = true;     //2    
    this.isUpdateMoney3         = true;     //3    
    this.playBossBGM            = -1;//boss
    // 
    this.fishPool               = new cc.NodePool("fish");//
    this.bulletPool             = new cc.NodePool("bullet");//
    this.currFishzIndex         = {};
    Date.prototype.Format       = function (fmt) { //author: meizz
        var o = {
            "M+": this.getMonth() + 1, //
            "d+": this.getDate(), //
            "h+": this.getHours(), //
            "m+": this.getMinutes(), //
            "s+": this.getSeconds(), //
            "q+": Math.floor((this.getMonth() + 3) / 3), //
            "S": this.getMilliseconds() //
        };
        if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
        for (var k in o)
            if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
        return fmt;
    }
    this.offLineMissileData     = null;//
    this.gameZoomX              = 1;//x
    this.gameZoomY              = 1;//y
    this.bgIndex                = -1;// 
}
