fish.onKill = function(res){
    let msg = JSON.parse(JSON.stringify(res));
    let BossLavaBasaltfishTypeId = -1;
    let total = 0;
    let myid = Number(glGame.user.userID);
    if(this.fishPoolData == null)return;
    if(this.playerInfo[Number(msg.uid)] != null){
        this.playerInfo[Number(msg.uid)].gold = msg.coin;
        msg.gold    = msg.coin;
        msg.seatNum = this.playerInfo[Number(msg.uid)].seatNum;
    }else{
        cc.error("")
        for (let k=0;k<msg.rewardMap.length;k++) {
            let data = msg.rewardMap[k];
            let fishTypeId = this.fishPoolData[Number(data.fishId)] != null ? Number(this.fishPoolData[Number(data.fishId)].fishTypeId) : 0;
            if(isNaN(Number(data.fishId))){
                cc.error(">1> ",data," msg : ",msg);
                continue;
            }
            let info = {
                fishId: Number(data.fishId),
                rewardGold : data.reward ? data.reward : 0,
                fishTypeId:fishTypeId,
                seatNum:msg.seatNum,
                killType:msg.cannonType
            }
            if(info.killType == CONST.CannonType.Bomb){
                info.delayDieTime = k * CONST.DelayDieTime;
            }
            glGame.emitter.emit(CONST.clientEvent.onKillFish,info);//
        }
        if(myid == Number(msg.uid)){
            this.gold = Number(msg.coin);
            this.checkGold();
            // cc.error(">>   2")
            glGame.emitter.emit(CONST.clientEvent.myUpdateMoney);
        }else {
            // cc.error(">>   2")
            glGame.emitter.emit(CONST.clientEvent.updateShootGold,msg);
        }
        return;
    }
    for (let k=0;k<msg.rewardMap.length;k++){
        let data = msg.rewardMap[k];
        if(data.reward != undefined){
            total += Number(this.formatMoney(Number(data.reward+"")));
        }
        if(isNaN(Number(data.fishId))){
            cc.error(">2> ",data," msg : ",msg);
            continue;
        }
        let fishTypeId = this.fishPoolData[Number(data.fishId)] != null ? Number(this.fishPoolData[Number(data.fishId)].fishTypeId) : 0;
        let localData  = this.json_fishTable[this.fishPoolData[Number(data.fishId)]];
        let info = {
            fishId: Number(data.fishId),
            rewardGold : data.reward ? data.reward : 0,
            seatNum:msg.seatNum,
            uid:msg.uid,
            fishTypeId:fishTypeId,
            killType:msg.cannonType
        }
        if(info.killType == CONST.CannonType.Bomb){
            info.delayDieTime = k * CONST.DelayDieTime;
        }
        if(localData){
            cc.warn("id:",Number(data.fishId),">fTId:",fishTypeId,"n:",localData.fishName,"resGroupId:",localData.resGroupId)
        }
        if(fishTypeId == CONST.BossGodOfWealth){//boss1 
            let sprinkInfo = {uid:msg.uid,fishId:Number(data.fishId),seatNum:msg.seatNum, rewardGold : data.reward ? data.reward : 0,info:info,uid:msg.uid};
            glGame.emitter.emit(CONST.clientEvent.onSshock);
            glGame.emitter.emit(CONST.clientEvent.sprinkleRedBag,sprinkInfo);
        }
        if(fishTypeId == CONST.YuRuyi){// 
            if(data.rewardArray != null && data.rewardMultiple != null){
                // cc.error(">>===========    ============== 1  >>");
                let turntableInfo = {
                    uid:msg.uid,
                    fishId:Number(data.fishId),
                    seatNum:msg.seatNum,
                    rewardGold : data.reward ? data.reward : 0,
                    rewardArray:data.rewardArray,
                    rewardMultiple:data.rewardMultiple,
                    msg:msg
                };
                if(myid == Number(msg.uid)){
                    this["isUpdateMoney"+msg.seatNum] = false;
                    turntableInfo.type = CONST.AwardType.RUYI;
                    glGame.emitter.emit(CONST.clientEvent.showTurntable,turntableInfo);
                }else{
                    turntableInfo.type = CONST.AwardType.RUYI;
                    glGame.emitter.emit(CONST.clientEvent.otherPlayerShowSpecialAward,turntableInfo);
                }
            }else{
                cc.warn("  rewardArray  rewardMultiple   data: ",data);
            }
        }
        if(fishTypeId == CONST.BossLavaBasalt) {// 
            BossLavaBasaltfishTypeId = fishTypeId;
            glGame.emitter.emit(CONST.clientEvent.onSpecialBomb,info);
        }
        if(fishTypeId == CONST.TreasureBowl){// 
            // cc.error(">>===========    ============== 2  >>");
            let turntableInfo = {
                uid:msg.uid,
                fishId:Number(data.fishId),
                seatNum:msg.seatNum,
                rewardGold : data.reward ? data.reward : 0,
                msg:msg
            };
            if(myid == Number(msg.uid)){
                this["isUpdateMoney"+msg.seatNum] = false;
                cc.warn(">>>   data.reward "+data.reward)
                turntableInfo.type = CONST.AwardType.CORNUCOPIA;
                glGame.emitter.emit(CONST.clientEvent.showTurntable,turntableInfo);
            }else{
                turntableInfo.type = CONST.AwardType.CORNUCOPIA;
                glGame.emitter.emit(CONST.clientEvent.otherPlayerShowSpecialAward,turntableInfo);
            }
        }
        let specialCannonInfo = {uid:msg.uid,fishId:Number(data.fishId),seatNum:msg.seatNum};
        if(data.newCannonType != null){
            if(data.newCannonType == CONST.CannonType.Bomb) {// 
                cc.error(">> XXX   newCannonType error  msg: ", msg);
            }
            if(this.playerInfo[Number(msg.uid)].cannonType != null && this.playerInfo[Number(msg.uid)].cannonType != Number(data.newCannonType)){
                specialCannonInfo.isNew = true;
                if(myid == Number(msg.uid)){
                    cc.warn("GETCANNON ===>>>    ")
                }
            }else{
                specialCannonInfo.isNew = false;
                if(myid == Number(msg.uid)) {
                    cc.warn("GETCANNON ===>>>    ")
                }
            }
            this.playerInfo[Number(msg.uid)].cannonType     =  Number(data.newCannonType);
            this.playerInfo[Number(msg.uid)].cannonAmount   =  Number(data.newCannonAmount);
            if(data.maxFish != null){// 
                this.playerInfo[Number(msg.uid)].hitMax     =  Number(data.maxFish);
            }
            // if(myid == Number(msg.uid)) {
            //     glGame.emitter.emit(CONST.clientEvent.fishSound,"getSpcliaUser");//
            // }
            glGame.emitter.emit(CONST.clientEvent.getSpecialCannon,specialCannonInfo);
            glGame.emitter.emit(CONST.clientEvent.checkSpecialBullet);
        }
        if(fishTypeId == CONST.BossGodOfWealth || fishTypeId == CONST.YuRuyi || fishTypeId == CONST.TreasureBowl) {
            //
            //
            //
            //  
        }else{
            glGame.emitter.emit(CONST.clientEvent.playCoinEffect, info);//
        }
        if(fishTypeId == CONST.BossGodOfWealth && data.lives){
            //
        }else{
            //
            glGame.emitter.emit(CONST.clientEvent.onKillFish,info);//
        }

    }
    if(msg.cannonType != null && msg.cannonType != CONST.CannonType.Normal && msg.cannonType != CONST.CannonType.Bomb){//
        let speciaInfo = {cannonType:msg.cannonType,total:total,coin:msg.coin,uid:msg.uid,seatNum:msg.seatNum,totalReward:this.formatMoney(msg.totalReward)};

        if(BossLavaBasaltfishTypeId == CONST.BossLavaBasalt) {// 
            cc.warn(">>      msg: ",msg);
        }
        cc.warn("1 =====>    "+total , "  totalReward  "+speciaInfo.totalReward , " seatNum "+msg.seatNum+" msg.cannonType "+msg.cannonType);
        glGame.emitter.emit(CONST.clientEvent.specialBulletStatistics, speciaInfo);
    }
    //   
    if(this["isUpdateMoney"+msg.seatNum]){
        if(myid == Number(msg.uid)){
            this.gold = Number(msg.coin);
            this.checkGold();
            // cc.error(">>   4")
            glGame.emitter.emit(CONST.clientEvent.myUpdateMoney);
        }else {
            // cc.error(">>   4")
            glGame.emitter.emit(CONST.clientEvent.updateShootGold,msg);
        }
    }
}
