//2  setAngle 
fish.getP1ToP2Angle = function(startPos,endPos){
    let comVec = new cc.Vec2(1, 0);                 //x, 23180
    let dirVec = startPos.sub(endPos);              //startPosendPos
    let radian = cc.v2(dirVec).signAngle(comVec);   //()
    let ofxAng = 270;                               //
    let angle  = -(Math.floor(cc.misc.radiansToDegrees(radian))+ofxAng);//
    return angle;
}
//  
fish.getFloat = function(value){
    return (Number(value).div(100)).toString();
}
//
fish.getFixNumber = function (value,facetionDigits = 2) {
    let value1 = Number(value).div(10);
    value = Number(value).div(100);
    if (isNaN(value)) return;
    if (~~value === value) {
        return value.toString();
    } else if (~~value1 === value1) {
        return value.toFixed(facetionDigits-1 < 0 ? 0 : facetionDigits-1 );
    } else {
        return value.toFixed(facetionDigits);
    }
}
//
fish.getRandomNum = function(Min,Max){
    let Range = Number(Max) - Number(Min);
    let Rand = Math.random();
    return (Min + Math.round(Rand * Range));
}
// - 
fish.playSpine = function(spineNode,loop,playerdClose,spineName,cb = null,nextPlyName = null,nextLoop = false){
    spineNode.active = true;
    let spine = spineNode.getComponent(sp.Skeleton);
    spine.setCompleteListener((trackEntry, loopCount)=>{
        let name = trackEntry.animation ? trackEntry.animation.name : "";
        if(nextPlyName != null){
            if (name == spineName) {
                spine.setAnimation(0,nextPlyName,nextLoop);
                if(playerdClose){
                    spineNode.active = false;
                }
                if(cb){
                    cb(name);
                }
            }
        }else{
            if(playerdClose){
                spineNode.active = false;
            }
            if(cb){
                cb(name);
            }
        }
    })
    spine.setAnimation(0,spineName,loop);
}
