fish.getClickArea = (fish,width,height)=>{
    const Harf = 2;
    const OffsexX = -50;
    const OffsexY = -50;
    if(((cc.winSize.width/Harf - OffsexX ) - Math.abs(fish.x)) > 0 && ((cc.winSize.height/Harf - OffsexY) - Math.abs(fish.y)) > 0){
        return true;
    }else{
        return false;
    }
}
